.autorisationForm {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 15px;
    background-color: #fff;
    border-radius: 4px;
    font-size: 18px;

  .title {
    font-size: 32px;
    font-weight: 900;
    line-height: 40px;
    margin-bottom: 10px;
  }

  .error {
    display: none;
    margin: 8px 0
  }

  .errorText{
    color: red;
    display: block;
  }
}